<template>
  <div class="root">
    <div v-for="item in displayText">
      <DisplayQA :Question=item.Question :Answer=item.Answer />
    </div>
  </div>
</template>

<script>
import DisplayQA from "../../components/DisplayQA";
export default {
  name: "InterView",
  data(){
    return {
      displayText:[
        {
          Question: 'Q: 面试轮次是什么样的？面试后多久可以拿到offer？',
          Answer:   'A: 本次面试流程分为专业面试及HR面试，面试通过后将在一周内进行offer沟通，请注意时刻保持电话顺畅哦。'
        },
        {
          Question: 'Q: 必须现场参加面试吗？',
          Answer:   'A: 线上面试、现场面试两种方式均支持，可根据自身实际情况进行选择。'
        }
      ]
    }
  },
  components:{
    DisplayQA
  }
}
</script>

<style scoped>

</style>



